<template>
  <div id="detail">
    <detail-nav-bar class="detail-nav"  @itemClick="itemClick" ref="nav"/>
    <scroll class="content" ref="scroll" :probeType="3" @scroll="contentScroll">
      <detail-swiper :top-images="topImages"/>
      <detail-base-info :goods="goods"/>
      <detail-shop-info :shop="shop"/>
      <detail-goods-info :detail-info="detailInfo"/>
      <detail-comment-info :comment-info="commentInfo" ref="comment"/>
      <detail-param-info :paramInfo="paramInfo" ref="params"/>
      <good-list :goods="recommends" ref="recommend"/>
    </scroll>
    <back-top @click.native="backClick" v-show="isShowBackTop"/>
    <detail-bottom-bar @addCart="addToCart"/>
    <toast :message="message" :is-show="isShow"/>
  </div>
</template>

<script>
  import DetailNavBar from "./childComps/DetailNavBar";
  import DetailSwiper from "./childComps/DetailSwiper";
  import DetailBaseInfo from "./childComps/DetailBaseInfo";
  import DetailShopInfo from "./childComps/DetailShopInfo";
  import Scroll from "components/common/scroll/Scroll";
  import DetailGoodsInfo from "./childComps/DetailGoodsInfo";
  import DetailParamInfo from "./childComps/DetailParamInfo";
  import DetailCommentInfo from "./childComps/DetailCommentInfo";
  import GoodList from "components/content/goods/GoodList";
  import DetailBottomBar from "./childComps/DetailBottomBar";
  import BackTop from "../../components/content/backTop/BackTop";
  import Toast from "../../components/common/toast/Toast";

  import {debounce} from "common/utils";
  import {getDetail, Goods, Shop, GoodsParam,getRecommend} from "network/detail";

  export default {
    name: "Detail",
    components: {
      DetailNavBar,
      DetailSwiper,
      DetailBaseInfo,
      DetailShopInfo,
      Scroll,
      DetailGoodsInfo,
      DetailParamInfo,
      DetailCommentInfo,
      GoodList,
      DetailBottomBar,
      BackTop,
      Toast
    },
    data() {
      return {
        id: null,
        topImages: [],
        goods: {},
        shop: {},
        detailInfo: {},
        paramInfo: {},
        commentInfo: {},
        recommends: [],
        themeTopYs: [],
        getThemeTopY: null,
        isShowBackTop: false,
        isShow: false,
        message: ''
      }
    },
    created() {
      this.id = this.$route.params.id
      getDetail(this.id).then(res => {
        const data = res.result
        this.topImages = data.itemInfo.topImages

        //获取商品信息
        this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services)
        //店铺信息
        this.shop = new Shop(data.shopInfo)
        //详细数据
        this.detailInfo = data.detailInfo;
        //参数信息
        this.paramInfo = new GoodsParam(data.itemParams.info, data.itemParams.rule)
        //评论信息
        if(data.rate.cRate !== 0) {
          this.commentInfo = data.rate.list[0]
        }

        this.getThemeTopY = debounce(() => {
          this.themeTopYs = [];
          this.themeTopYs.push(0);
          if (this.$refs.comment && this.$refs.params && this.$refs.recommend) {
            this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
            this.themeTopYs.push(this.$refs.params.$el.offsetTop);
            this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
          }


          this.themeTopYs.push(Number.MAX_VALUE);//contentScroll()方法二优化
        },500)
      })
      getRecommend().then(res => {
        this.recommends = res.data.list
      })

    },
    mounted() {
      const refresh = debounce(this.$refs.scroll.refresh,600)
      //监听图片加载完成
      this.$bus.$on('imageLoad',() => {
        refresh()
        this.getThemeTopY()
      })
    },
    methods: {
      itemClick(index) {
        this.$refs.scroll.scrollTo(0,-(this.themeTopYs[index] - 44),300)
      },
      contentScroll(position) {
        const positionY = -position.y +44
        // 方法一：
        // if (positionY >= this.themeTopYs[0] && positionY < this.themeTopYs[1]) {
        //   this.currentIndex = 0;
        //   this.$refs.nav.currentIndex = this.currentIndex;
        // } else if (positionY >= this.themeTopYs[1] && positionY < this.themeTopYs[2]) {
        //   this.currentIndex = 1;
        //   this.$refs.nav.currentIndex = this.currentIndex;
        // } else if (positionY >= this.themeTopYs[2] && positionY < this.themeTopYs[3]) {
        //   this.currentIndex = 2;
        //   this.$refs.nav.currentIndex = this.currentIndex;
        // } else {
        //   this.currentIndex = 3;
        //   this.$refs.nav.currentIndex = this.currentIndex;
        // }

        // 方法二：
        // let length = this.themeTopYs.length
        // this.themeTopYs.forEach((currentThemeTopY, index) => {
        //   if (this.currentIndex !== index && ((index < length - 1 && positionY >= this.themeTopYs[index] &&
        //   positionY < this.themeTopYs[index+1]) || (index === length - 1 && positionY >=this.themeTopYs[index]))) {
        //     this.currentIndex = index;
        //     this.$refs.nav.currentIndex = this.currentIndex;
        //   }
        // })

        // 方法二优化：
        // [0, 2613, 3969, 4164, Number.MAX_VALUE]
        // positoinY 在 0 和 2613之间，index = 0
        // positoinY 在 =2613 和 3969之间，index = 1
        // positoinY 在 =3969 和 4164之间，index = 2
        // positoinY 在 4164 和 非常大的值之间，index = 3
        let length = this.themeTopYs.length
        for (let i = 0; i < length-1; i++) {
          if (this.currentIndex !== i && (positionY >= this.themeTopYs[i] &&
              positionY < this.themeTopYs[i+1])) {
            this.currentIndex = i;
            this.$refs.nav.currentIndex = this.currentIndex;
          }
        }

        //BackTop
        this.isShowBackTop = (-position.y) > 1000
      },
      backClick() {
        this.$refs.scroll.scrollTo(0, 0)
      },
      addToCart() {
        const product = {};
        product.title = this.goods.title;
        product.image = this.topImages[0];
        product.desc = this.goods.desc;
        product.price = this.goods.realPrice;
        product.id = this.id;

        this.$store.dispatch('addCart',product).then(res => {
          this.isShow = true;
          this.message = res;

          setTimeout(() => {
            this.message = ''
            this.isShow = false
          },1500)

          // console.log(res);
          // console.log(this.$toast);
          // this.$toast.methods.show()
          // console.log(this.$toast.data());
        })


      }
    },
  }
</script>

<style scoped>
  #detail {
    position: relative;
    z-index: 9;
    background-color: #fff;
    height: 100vh;
    overflow: hidden;
  }
  .detail-nav {
    position: relative;
    z-index: 9;
    background-color: #fff;
  }
  .content {
    height: calc(100% - 93px);
  }
</style>